<template>
  <v-card>
    <v-card-title>
      <h2 class="headline mb-0">Register Users</h2>
    </v-card-title>
    <v-card-text>
      <form @submit.prevent="createUser">
        <v-card-text v-if="errors">
          <span class="text-danger" v-for="(error, index) in errors" :key="index">{{error}}</span>
        </v-card-text>
        <v-text-field

          label="Name"
          required
          :rules=[validators.required]
          outlined
          dense

        ></v-text-field>
        <v-text-field
          v-model="user.email"
          label="Email"
          :rules="[validators.emailValidator, validators.required]"
          required
          outlined
          dense

        ></v-text-field>
        <v-select
          v-model="user.role"
          :items="roles"
          label="Role"
          outlined
          :rules="[validators.required]"
          dense
        ></v-select>
        <v-text-field
          v-model="user.password"
          label="Password"
          :type="isPasswordVisible ? 'text' : 'password'"
          :rules="[validators.required]"
          required
          outlined
          dense
          hide-details
          :append-icon="isPasswordVisible ? icons.mdiEyeOffOutline:icons.mdiEyeOutline"
          @click:append="isPasswordVisible = !isPasswordVisible"
        ></v-text-field>
        <v-text-field
          v-model="user.re_password"
          label="Confirm Password"
          type="isConfirmPasswordVisible ? 'text' : 'password'"
          :rules="[required]"
          required
          outlined
          dense
          hide-details
          class="mt-2"
          :append-icon="isConfirmPasswordVisible ? icons.mdiEyeOffOutline:icons.mdiEyeOutline"
          @click:append="isConfirmPasswordVisible = !isConfirmPasswordVisible"
        ></v-text-field>
        <v-btn type="submit" color="primary" class="mt-3">
          Register
        </v-btn>
      </form>
      </v-card-text>
    </v-card>

</template>

<script>
import axios from '@axios'
import { required, emailValidator } from '@core/utils/validation'
import { mdiEyeOffOutline, mdiEyeOutline } from '@mdi/js'
export default {
name: 'CreateUser',
data() {
return {
  user: {

    email: '',
    role: '',
    password: '',
    re_password: ''
  },
  isPasswordVisible: false,
  isConfirmPasswordVisible: false,
roles: [
  { text: 'Admin', value: 'admin' },
  { text: 'Manager', value: 'manager' },
  { text: 'Staff', value: 'staff' }
],
errors:[],

}
},
methods: {
createUser() {
  this.errors = [];
  if (this.user.password !== this.user.re_password) {
    this.errors.push('Password does not match');
  if (this.errors) {
    return;
  }
  }
  axios.post('/auth/users/', this.user)
  .then(response=>{
    this.$router.push('/apps/user/list')})
  .catch(
    (error) => {
      this.errors = error.response.data;
      console.log(error.response.data)})

},
},
setup(){

  return{
    validators:{
    required,
    emailValidator,
    },

    icons:{
      mdiEyeOffOutline,
      mdiEyeOutline,
    }

  }

}
}
</script>

